import veeValidate from 'vee-validate'
import zh_CN from 'vee-validate/dist/locale/zh_CN'
export default function (Vue) {
    // vee表单验证
    Vue.use(veeValidate)
    veeValidate.Validator.localize('ZH_CN', {
        messages: {
            ...zh_CN.messages,
            is: '与第一次密码不符'
        },
        attributes: {
            'phone': '电话号码',
            password: '密码',
            sendCode: '验证码',
            Isagree: '协议'
        }
    })
    // 使用extend自定义一个校验规则
    veeValidate.Validator.extend('Isagree', {
        validate: (value) => {
            // value代表绑定当前规则的表单的value值
            // 返回一个布尔值,代表是否符合规则
            // 多选框获取就是布尔,返回一个布尔
            return value
        },
        getMessage: (e) => {
            // 定制错误提示
            return '必须同意' + e
        }
    })
}