<template>
  <div class="type-nav">
    <div class="container">
      <div
        @mouseenter="homeShow = true"
        @mouseleave="eatehShow ? (homeShow = false) : ''"
      >
        <h2 class="all">全部商品分类</h2>
        <transition name="nav">
          <div class="sort" v-show="homeShow">
            <div
              class="all-sort-list2"
              @mouseleave=";(isShowRight = -1), (isEnter = false)"
              @mouseenter="isEnter = true"
              @click="toSearch"
            >
              <div
                class="item"
                v-for="(category1, index) in categoryList"
                :key="category1.categoryId"
                :class="isShowRight === index ? 'active' : ''"
                @mouseenter="debounceEnd(index)"
              >
                <h3>
                  <a
                    href="javascript:;"
                    :data-category1-id="category1.categoryId"
                    :data-category-name="category1.categoryName"
                    >{{ category1.categoryName }}</a
                  >
                </h3>
                <div class="item-list clearfix">
                  <div class="subitem">
                    <dl
                      class="fore"
                      v-for="category2 of category1.categoryChild"
                      :key="category2.categoryId"
                    >
                      <dt>
                        <a
                          href="javascript:;"
                          :data-category2-id="category2.categoryId"
                          :data-category-name="category2.categoryName"
                          >{{ category2.categoryName }}</a
                        >
                      </dt>
                      <dd>
                        <em
                          v-for="category3 of category2.categoryChild"
                          :key="category3.categoryId"
                        >
                          <a
                            href="javascript:;"
                            :data-category3-id="category3.categoryId"
                            :data-category-name="category3.categoryName"
                            >{{ category3.categoryName }}</a
                          >
                        </em>
                      </dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
      <nav class="nav">
        <a href="###">服装城</a>
        <a href="###">美妆馆</a>
        <a href="###">尚品汇超市</a>
        <a href="###">全球购</a>
        <a href="###">闪购</a>
        <a href="###">团购</a>
        <a href="###">有趣</a>
        <a href="###">秒杀</a>
      </nav>
    </div>
  </div>
</template>

<script>
import { throttle } from 'lodash'
import { mapState } from 'vuex'
export default {
  name: 'TypeNav',
  data() {
    return {
      isShowRight: -1,
      isEnter: false,
      homeShow: true,
      eatehShow: false,
    }
  },
  mounted() {
    this.debounceEnd = throttle(this.changeIndex, 100)
  },
  computed: {
    ...mapState('category', ['categoryList']),
  },
  methods: {
    // debounce() {
    //   let lastTime = 0
    //   return function (index) {
    //     let nowTime = Date.now()
    //     if (nowTime - lastTime < 100) {
    //       return
    //     }
    //     lastTime = nowTime
    //     this.isShowRight = index
    //   }
    // },
    changeIndex(index) {
      if (!this.isEnter) return
      this.isShowRight = index
    },
    toSearch(e) {
      if (e.target.nodeName === 'A') {
        const { category1Id, category2Id, category3Id, categoryName } =
          e.target.dataset
        this.$router.push({
          name: 'search',
          query: {
            category1Id,
            category2Id,
            category3Id,
            categoryName,
          },
          params: this.$route.params,
        })
      }
    },
  },
  watch: {
    $route: {
      immediate: true,
      handler(newVal) {
        if (newVal.name !== 'home') {
          this.homeShow = false
          this.eatehShow = true
        }
      },
    },
  },
}
</script>

<style lang="less" scoped>
.nav-enter-active,
.nav-leave-active {
  transition: all 0.2s linear;
}
.nav-enter,
.nav-leave-to {
  opacity: 0;
}
.type-nav {
  border-bottom: 2px solid #e1251b;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;

    .all {
      width: 210px;
      height: 45px;
      background-color: #e1251b;
      line-height: 45px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        height: 45px;
        margin: 0 22px;
        line-height: 45px;
        font-size: 16px;
        color: #333;
      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 45px;
      width: 210px;
      height: 461px;
      position: absolute;
      background: #fafafa;
      z-index: 999;

      & .active {
        background-color: orange;
        color: #fff;
        .item-list {
          display: block !important;
        }
      }
      .all-sort-list2 {
        .item {
          h3 {
            a {
              line-height: 30px;
              font-size: 14px;
              font-weight: 400;
              overflow: hidden;
              padding: 0 20px;
              margin: 0;
              display: block;
              color: #333;
            }
          }

          .item-list {
            display: none;
            position: absolute;
            width: 734px;
            min-height: 460px;
            background: #f7f7f7;
            left: 210px;
            border: 1px solid #ddd;
            top: 0;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                }

                dt {
                  float: left;
                  width: 54px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;
                }

                dd {
                  float: left;
                  width: 415px;
                  padding: 3px 0 0;
                  overflow: hidden;

                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    border-left: 1px solid #ccc;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
