import Mock from "mockjs";
import banner from './banner'
import floor from './floor'
import address from './address'
Mock.mock('/apiMock/banner/list', 'get', () => {
    return {
        code: 200,
        message: 'ok',
        data: banner
    }
})
Mock.mock('/apiMock/floor/list', 'get', () => {
    return {
        code: 200,
        message: 'ok',
        data: floor
    }
})
Mock.mock('/apiMock/address/list', 'get', () => {
    return {
        code: 200,
        message: 'ok',
        data: address
    }
})  