<template>
  <div class="swiper-container" id="mySwiper" ref="swiper">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="item in swiperList" :key="item.id">
        <img :src="item.imgUrl" />
      </div>
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination"></div>

    <!-- 如果需要导航按钮 -->
    <div class="swiper-button-prev"></div>
    <div class="swiper-button-next"></div>
  </div>
</template>

<script>
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'
export default {
  props: ['swiperList'],
  watch: {
    swiperList: {
      immediate: true,
      handler(newVal) {
        // 当第一次传入时,异步请求的数据会执行两次,重复渲染出现错误
        if (newVal.length <= 0) return
        // 当数据改变后,并且页面dm渲染完毕后执行激活swiper
        this.$nextTick(() => {
          this.mokeSwiper()
        })
      },
    },
  },
  methods: {
    mokeSwiper() {
      // 多处创建Swiper对象,监听同一给类名,这样在打包时就会使得全部变为同时监听这样会导致一次点击会有两次swiper跳动
      // 所以要为swiper设置不同的类名
      // vu中获取节点的方式是ref
      // 所以直接使用ref获取然后使用this.refs接收即可
      new Swiper(this.$refs.swiper, {
        loop: true, // 循环模式选项
        paginationClickable: true,
        // 如果需要分页器
        pagination: {
          el: '.swiper-pagination',
          // 分页器切换
        },

        // 如果需要前进后退按钮
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },

        // 如果需要滚动条
      })
    },
  },
}
</script>

<style></style>
